import React, { useState } from 'react'

function App() {
  const questions = [
    {
      questionText: 'How many bibles were stacked on the right of GS?',
      answerOptions: [
        { answerText: 9, isCorrect: false },
        { answerText: 5, isCorrect: false },
        { answerText: 3, isCorrect: true },
        { answerText: 6, isCorrect: false },
      ],
    },
    {
      questionText:
        'According to Phillipians 3:8 The knowledge of Christ is what?',
      answerOptions: [
        { answerText: 'Glorious', isCorrect: false },
        { answerText: 'Excellent', isCorrect: true },
        { answerText: 'Amazing', isCorrect: false },
        { answerText: 'Sanctifying', isCorrect: false },
      ],
    },
    {
      questionText:
        "It will take determination, purposefulness and what to enter God's rest?",
      answerOptions: [
        { answerText: 'Faith', isCorrect: false },
        { answerText: 'The Holy Spirit', isCorrect: false },
        { answerText: 'Discipline', isCorrect: true },
        { answerText: 'Repentance', isCorrect: false },
      ],
    },
    {
      questionText: 'What animals are the two ornaments on GS shelf?',
      answerOptions: [
        { answerText: 'Lamb & Sheep', isCorrect: false },
        { answerText: 'Lion & Dog', isCorrect: false },
        { answerText: 'Whale & Ox', isCorrect: false },
        { answerText: 'Eagle & Horse', isCorrect: true },
      ],
    },
    {
      questionText:
        'There is a river, the streams whereof shall make glad the city of God, the holy place of the tabernacles of the most High. is from what Psalm and verse?',
      answerOptions: [
        { answerText: 'Psalm 23:5 ', isCorrect: false },
        { answerText: 'Psalm 119:78', isCorrect: false },
        { answerText: 'Psalm 46:4', isCorrect: true },
        { answerText: 'Psalm 50:2', isCorrect: false },
      ],
    },
    {
      questionText: 'According to GS, we are?',
      answerOptions: [
        { answerText: 'Refugees', isCorrect: true },
        { answerText: 'Fugatives', isCorrect: false },
        { answerText: 'Aliens', isCorrect: false },
        { answerText: 'Asylum Seekers', isCorrect: false },
      ],
    },
    {
      questionText:
        'Accoring to GS,the more you receive the word, the more you are loading your system with what?',
      answerOptions: [
        { answerText: 'Protection', isCorrect: false },
        { answerText: 'Spirit Food', isCorrect: false },
        { answerText: 'Resistance', isCorrect: false },
        { answerText: 'Shock Absorbers', isCorrect: true },
      ],
    },
    {
      questionText: 'You can only enter rest through what?',
      answerOptions: [
        { answerText: 'Belief', isCorrect: false },
        { answerText: 'Faith', isCorrect: true },
        { answerText: 'Jesus', isCorrect: false },
        { answerText: 'Hope', isCorrect: false },
      ],
    },
  ]

  const [currentQuestion, setCurrentQuestion] = useState(0)

  const [showScore, setShowScore] = useState(false)

  const [score, setScore] = useState(0)

  const handleClick = isCorrect => {
    if (isCorrect === true) {
      setScore(score + 1)
      alert('That is Correct')
    } else {
      alert('That is Incorrect')
    }

    const nextQuestion = currentQuestion + 1
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion)
    } else {
      setShowScore(true)
    }
  }
  return (
    <div className="app">
      {showScore ? (
        <div className="score-section">
          You scored {score} out of {questions.length}
        </div>
      ) : (
        <>
          <div className="question-section">
            <div className="question-count">
              <span>Question {currentQuestion + 1}</span>/{questions.length}
            </div>
            <div className="question-text">
              {questions[currentQuestion].questionText}
            </div>
          </div>
          <div className="answer-section">
            {questions[currentQuestion].answerOptions.map(answer => (
              <button onClick={() => handleClick(answer.isCorrect)}>
                {answer.answerText}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  )
}

export default App
